.input {
  max-width: rem(320);
  margin: 0 auto;
  position: relative;
  border: 1px solid $grey;
  border-radius: rem(40);
  display: flex;
  background: lighten($color: $grey, $amount: 16);
}

.input__form {
  border-radius: rem(40);
  font: $desktop-body3-regular;
  padding: rem(12) rem(24);
  box-sizing: border-box;
  text-align: left;
}

.input__button {
  position: absolute;
  right: 0;
  flex: 1;
}

@media screen and (min-width: 1200px) {
  .input {
    margin: 0;
  }
}
